* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
  font-family: "Inconsolata", monospace;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-height: 10vh;
  background: #0f1824;
}

.logo {
  color: white;
  font-size: 2rem;
  letter-spacing: 5px;
}

.nav-links {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 30%;
}

.nav-links a {
  color: white;
  font-size: 1.35rem;
  color: #c71f5c;
}

main {
  background: #243447;
  min-height: 90vh;
}

.main-content-container {
  padding: 10vh 7%;
  width: 75%;
}

.main-intro {
  font-size: 1.4rem;
  color: #c71f5c;
}
.main-name,
.main-saying,
.main-description {
  margin-top: 5vh;
}

.main-name {
  font-size: 4rem;
  color: white;
}

.main-saying {
  font-size: 2.76rem;
  color: #8892b0;
}

.main-description {
  color: #8892b0;
  font-size: 1.35rem;
  margin: 5vh 0;
}

section {
  background: #0f1824;
  color: white;
  min-height: 100vh;
}

.section-title {
  font-size: 2rem;
  padding: 5%;
}

section p {
  font-size: 1.4rem;
  margin-top: 3vh;
}

.about-flex-content {
  display: flex;
  flex-direction: row;
  padding: 0% 5%;
}

.about-text-container {
  width: 50vw;
}

.technology-list {
  margin-top: 5vh;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 30%;
}

.profile-pic {
  width: 40vw;
  height: 38vh;
}

.works {
  background: #243447;
  color: white;
}

.works-flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2% 5%;
}

.works-flex-row {
  display: flex;
  flex-direction: row;
  gap: 5vw;
  margin-bottom: 8vh;
}

.works-img {
  width: 35vw;
}

.works-description {
  margin-top: 3vh;
  width: 85%;
}

.works-technologies {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: #8892b0;
  margin: 5vh 0;
  width: 83%;
}

.works-technologies a {
  color: #c71f5c;
}

.works-technologies a:hover {
  color: white;
}

footer {
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
  background-color: #0f1824;
  color: white;
  padding: 5% 0;
  height: 25vh;
}

footer p {
  margin: 3vh 0;
}

footer a {
  color: #c71f5c;
}
